"use client"

import { useState, useEffect } from "react"
import { X, Mail, Gift } from "lucide-react"
import { Button } from "@/components/ui/button"

interface NewsletterModalProps {
  isOpen: boolean
  onClose: () => void
}

export function NewsletterModal({ isOpen, onClose }: NewsletterModalProps) {
  const [email, setEmail] = useState("")
  const [isVisible, setIsVisible] = useState(false)

  // Handle animation
  useEffect(() => {
    if (isOpen) {
      setIsVisible(true)
    } else {
      const timer = setTimeout(() => setIsVisible(false), 300)
      return () => clearTimeout(timer)
    }
  }, [isOpen])

  if (!isVisible && !isOpen) return null

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    // Handle subscription
    console.log("Subscribing from modal:", email)
    setEmail("")
    onClose()
    alert("Welcome to the club! Check your email for your bonus.")
  }

  return (
    <div 
      className={`fixed inset-0 z-[100] flex items-center justify-center p-4 transition-opacity duration-300 ${
        isOpen ? "opacity-100" : "opacity-0"
      }`}
    >
      {/* Backdrop */}
      <div 
        className="absolute inset-0 bg-black/60 backdrop-blur-sm"
        onClick={onClose}
      />

      {/* Modal Content */}
      <div 
        className={`relative w-full max-w-md overflow-hidden rounded-2xl bg-background border border-foreground/10 shadow-2xl transition-all duration-300 transform ${
          isOpen ? "scale-100 translate-y-0" : "scale-95 translate-y-4"
        }`}
      >
        {/* Close Button */}
        <button 
          onClick={onClose}
          className="absolute right-4 top-4 z-10 rounded-full p-1 text-muted-foreground hover:bg-foreground/10 hover:text-foreground transition-colors"
        >
          <X className="h-5 w-5" />
        </button>

        {/* Header / Image Area */}
        <div className="bg-gradient-to-br from-purple-600 to-pink-600 p-8 text-center text-white">
          <div className="mx-auto mb-4 flex h-16 w-16 items-center justify-center rounded-full bg-white/20 backdrop-blur-md">
            <Gift className="h-8 w-8 text-white" />
          </div>
          <h2 className="text-2xl font-bold mb-2">Exclusive Bonus!</h2>
          <p className="text-white/90">
            Subscribe now and get 50 Free Spins on your first deposit.
          </p>
        </div>

        {/* Form Area */}
        <div className="p-6 sm:p-8">
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="space-y-2">
              <label htmlFor="modal-email" className="text-sm font-medium">
                Email Address
              </label>
              <div className="relative">
                <Mail className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                <input
                  id="modal-email"
                  type="email"
                  placeholder="you@example.com"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                  className="w-full pl-10 pr-4 py-2 rounded-md border border-input bg-background ring-offset-background focus:outline-none focus:ring-2 focus:ring-ring focus:border-input transition-all"
                />
              </div>
            </div>
            <Button type="submit" className="w-full bg-gradient-to-r from-purple-600 to-pink-600 hover:opacity-90 transition-opacity">
              Get My Bonus
            </Button>
            <p className="text-xs text-center text-muted-foreground">
              No spam, just the best offers. Unsubscribe anytime.
            </p>
          </form>
        </div>
      </div>
    </div>
  )
}
