"use client"

import { Mail } from "lucide-react"
import { Button } from "@/components/ui/button"

interface FloatingSubscribeButtonProps {
  onClick: () => void
  isVisible: boolean
}

export function FloatingSubscribeButton({ onClick, isVisible }: FloatingSubscribeButtonProps) {
  return (
    <div 
      className={`fixed bottom-6 right-6 z-40 transition-all duration-500 transform ${
        isVisible ? "translate-y-0 opacity-100" : "translate-y-20 opacity-0"
      }`}
    >
      <Button
        onClick={onClick}
        size="lg"
        className="rounded-full shadow-lg bg-gradient-to-r from-purple-600 to-pink-600 hover:shadow-xl hover:scale-105 transition-all duration-300 group px-6"
      >
        <Mail className="mr-2 h-5 w-5 group-hover:animate-bounce" />
        <span className="font-semibold">Subscribe</span>
      </Button>
    </div>
  )
}
